<template>
    <div>
        <form>
            <input type="text" v-model="countryName" placeholder="Search for a country...">
            <select name="region" v-model="regionName">
                <option value="" disabled selected>Filter by Region</option>
                <option v-for="filter in regionFilters" :key="filter" :value="filter">{{filter}}</option>
            </select>
        </form>
    </div>
</template>

<script>
    import {mapGetters} from 'vuex';
    export default {

        computed: {
            countryName: {
                get: function() {
                    return this.$store.getters.getCountryName
                },
                set: function(value) {
                    this.$store.commit('setCountryName', value)
                }
            },
            regionName: {
                get: function() {
                    return this.$store.getters.getRegionName
                },
                set: function(value) {
                    console.log('set')
                    this.$store.commit('setRegionName', value)
                }
            },
            ...mapGetters({
                regionFilters: 'getRegionFilters'
            })
        },
        methods: {
            filterResults() {
                return true
            }
        }
    }
</script>

<style scoped lang="scss">
    form {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 80%;
        margin: 1em auto;
        @media only screen and (max-width: 26em) {
            flex-direction: column;
        }
    }
    input[type="text"], select {
        font: 400 14px 'Nunito Sans', sans-serif;
        padding: 1em;
        border: none;
        border-radius: 4px;
        box-shadow:  0 .125rem .25rem rgba(0,0,0,.075);
        outline: none;
        @media only screen and (max-width: 26em) {
            margin: 1em 0;
        }
    }
    input[type="text"] {
        width: 40%;
        @media only screen and (max-width: 26em) {
            width: 100%;
            margin: 1em 0;
        }
    }
    .dark input[type="text"],.dark select {
        color:  hsl(0, 0%, 100%);
        background-color: hsl(207, 26%, 17%); 
    }
</style>