<template>
 <div id="tacopage">
    <header>
        <div class="container">
            <h1>Get your Tacos here!</h1>
                <FilterForm />
        </div>            
    </header> 
    <section  id="businessDetails" v-if="currentBusiness.id">                      
        <section class="details" >
            <button @click="goBack">Close</button>
            <h1>{{currentBusiness.name}}</h1>
            <div class="rating">
                <ion-icon name="star" v-for="n in parseInt(currentBusiness.rating)" :key="n"></ion-icon>
                <ion-icon name="star-half-outline" v-if="parseFloat(currentBusiness.rating) - parseInt(currentBusiness.rating) === .5"></ion-icon>
            </div>
            <span class="pricing">{{currentBusiness.price}}</span>
            <p class="hours">
                <span :class="[{closed: currentBusiness.isClosed === true}, 'status']">
                    <ion-icon name="time-outline" ></ion-icon>
                    <span v-if="currentBusiness.isClosed === true">Closed</span>
                    <span v-else>Open</span>
                </span>
                <span class="hours" v-if="currentBusiness.hours"> 
                <ul>
                    <li class="header">Hours</li>
                    <li v-for="hours in currentBusiness.hours[0].open" :key="hours.day">
                        <span class="day" v-if="hours.day === 0">Sunday:</span>
                        <span class="day" v-if="hours.day === 1">Monday:</span>
                        <span class="day" v-if="hours.day === 2">Tuesday:</span>
                        <span class="day" v-if="hours.day === 3">Wednesday:</span>
                        <span class="day" v-if="hours.day === 4">Thursday:</span>
                        <span class="day" v-if="hours.day === 5">Friday:</span>
                        <span class="day" v-if="hours.day === 6">Saturday:</span>
                        <span class="range">{{hours.start | convertTime }} to {{hours.end | convertTime }}</span></li>
                    </ul>
                </span>                           
            </p>
            <div class="contact">
            <p class="phone"><a target="_blank" :href="`tel:${currentBusiness.phone}`"><ion-icon name="call-outline"></ion-icon> {{currentBusiness.phone}}</a></p> 
                <p class="address"><a target="_blank" :href="`https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(currentBusiness.name)}+${encodeURIComponent(currentBusiness.location.address1)}+${encodeURIComponent(currentBusiness.location.city)}+${encodeURIComponent(currentBusiness.location.state)}`"><ion-icon name="map-outline"></ion-icon></a><span>{{currentBusiness.location.address1}}<br />{{currentBusiness.location.city}}, {{currentBusiness.location.state}}</span></p>
            </div>
            <p>                            
                <span class="category" v-for="(category, index) in currentBusiness.categories" :key="category.alias">{{category.title}}<span v-if="index != currentBusiness.categories.length - 1">,</span> </span>
            </p>
            <p>
                <span class="transactions">
                    <span class="pickup">
                        <ion-icon name="checkmark-circle-outline" v-if="currentBusiness.transactions.indexOf('pickup') != -1"></ion-icon>
                        <ion-icon name="close-circle-outline" v-else></ion-icon>
                        Pickup
                    </span>
                    <span class="delivery">
                        <ion-icon name="checkmark-circle-outline" v-if="currentBusiness.transactions.indexOf('delivery') != -1"></ion-icon>
                        <ion-icon name="close-circle-outline" v-else></ion-icon>
                        Delivery
                    </span>
                    <span class="takeout">
                        <ion-icon name="checkmark-circle-outline" v-if="currentBusiness.transactions.indexOf('takeout') != -1"></ion-icon>
                        <ion-icon name="close-circle-outline" v-else></ion-icon>
                        Takeout
                    </span>
                </span>
            </p>
        </section>
        <agile :fade="true" :dots="true" :navButtons="false">
            <div class="slide" v-for="(photo, index) in currentBusiness.photos" :key="index">
                <img :src="photo" :alt="currentBusiness.name" loading=lazy>
            </div>
        </agile>
    </section>
    </div>
</template> 

<script>
    import { mapGetters } from "vuex";
    import FilterForm from '@/components/Tacos/FilterForm.vue'
    export default {
        props: {
            slug: {
                type: String,
                required: true
            }
        },
        computed: {
            ...mapGetters({
                currentBusiness: 'getCurrentBusiness'
            })
        },
        components: {
            FilterForm
        },
        methods: {
            viewDetails() {                
                this.$store.dispatch('fetchBusiness', this.slug);
            },
            goBack() {
                return this.$router.go(-1);
            }
        },
        filters: {
            convertTime(value) {
                if (!value) return ''
                let hours24 = parseInt(value.substring(0, 2), 10);
                let hours = ((hours24 + 11) % 12) + 1;
                let amPm = hours24 > 11 ? 'pm' : 'am';
                let minutes = value.substring(2)
                return hours + ':' + minutes  + amPm
            }
        },
        created() {
            this.viewDetails();
        },
          metaInfo() {
            return {
            title: 'Taco Shop Demo',
            meta: [
                {
                name: 'description',
                content: 'Using Vue.js, Vuex, and Vue-agile to pull and display data from the Yelp API.'
                }
            ]
            }
        }
    }
</script>

<style lang="scss">

    #tacopage {
        font-family: 'Spartan', Arial, sans-serif;
        font-size: 14px;
        width: 100vw;

        header {
            position: sticky;
            top: 50px;
            background-color: #FFFFFF;
            border-bottom: 1px solid #DDDDDD;
            z-index: 100;
            width: 100%;
            background-image: url('../../assets/taco_background.jpg');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;

            h1 {
                color: #FFF;
                padding: .5em 1em;
                font-size: 3em;
                background-color: rgba(0,0,0,0.3);
                @media only screen and (max-width: 42em) {
                    font-size: 2em;
                    padding: .5em;
                }
            }
        }
    }
    .container {
        width: 80%;
        margin: 0 auto;
        @media only screen and (max-width: 27em) {
            width: 90%;
            margin: 0;
            padding: 0 1em;
        }
    }
    
    #businessDetails {
        box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
        padding: 1em;
        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        position: sticky;
        top: 200px;
        margin: 0 auto;

        .details {
            width: 100%;
            h1 {
                font-size: 1.5em;
                margin-bottom: .5em;
            }
        }
        @media only screen and (max-width: 42em) {
            flex-direction: column;
            width: 100%;
        }
    }
    .rating {
        font-size: 18px;
        line-height: 1em;
        color:  #ffab00;
        margin-bottom: .5em;
    }
    .detailArea {
        width: 100%;
         @media only screen and (max-width: 60em) {
            &.hide {display: none;}
        }
    }
    .agile {
        width: 50%;
        @media only screen and (max-width: 42em) {
            width: 100%
        }
    }
    .agile img {
        height: 400px;
    }
    .agile__slides {
        max-width: 100%;
    }
    .agile__dots {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    padding: 0;
    white-space: nowrap;
}
 .agile__dot {
    margin: 0 10px;
}
.agile__dot button {
    background-color: #eee;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: block;
    height: 10px;
    font-size: 0;
    line-height: 0;
    margin: 0;
    padding: 0;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
    width: 10px;
}
.agile__dot--current button, .agile__dot:hover button {
    background-color: #888;
}
.agile__slide {
    text-align: center;
}
.pricing {
    font-size: 18px;
    color: #1b5e20;
}
.hours .day {
    width: 8em;
    display: inline-block;
}
.hours .header {
    font-weight: bold;
    margin: .25em 0;
    border-bottom: 1px solid #DDDDDD;
}
.hours li {
    margin: .25em 0;
}
.status {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
    color:  #1b5e20;
    line-height:1em;
    font-size: 20px;
    ion-icon {
        height: 25px;
        width: 25px;
        margin-right: .5em;
    }
}
.transactions {
    span {
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        margin: 0 1em;
        ion-icon {
            height: 25px;
            width: 25px;
            &[name="checkmark-circle-outline"] {
                color: #1b5e20;
            }
            &[name="close-circle-outline"] {
                color: #b71c1c;
            }
        }
    }
}
.phone, .address {
    a {
        color: inherit;
        text-decoration: none;
        ion-icon {
            width: 25px;
            height: 25px;
            margin-right: .5em;
        }

    }
}
.address, .phone a{
    display: flex;
    flex-direction: row;
    align-items: center;
}
button {
        margin: .375rem;
    color: inherit;
    text-transform: uppercase;
    word-wrap: break-word;
    white-space: normal;
    cursor: pointer;
    border: 0;
    border-radius: .125rem;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    padding: .84rem 2.14rem;
    font-size: .81rem;
        color: #fff;
    background-color: #2e2e2e;
    cursor: pointer;
    border-radius: 4px;
    &:hover {
        box-shadow:  0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    }
    
}
</style>