<template>
    <div id="tacopage">
        <header>
            <div class="container">
                <h1>Get your Tacos here!</h1>
                 <FilterForm />
            </div>            
        </header>        
        <div class="content container">
            <ul id="locations" v-if="locations != undefined && locations.length > 0">                
                <li v-for="location in locations" :key="location.id" :class="{closed: location.is_closed}">
                    <router-link :to="{name: 'TacoDetails', params: {slug: location.id} }">
                        <section class="thumbnail">
                            <figure>
                                <img :src="location.image_url" :alt="location.name"  loading=lazy>
                            </figure>
                        </section>
                        <section class="details">
                            <p class="name">{{location.name}}</p>
                            <p class="rating">
                                <ion-icon name="star" v-for="n in parseInt(location.rating)" :key="n"></ion-icon>
                                <ion-icon name="star-half-outline" v-if="parseFloat(location.rating) - parseInt(location.rating) === .5"></ion-icon></p>
                            <p class="city">{{location.location.city}}</p>
                        </section>
                    </router-link>
                </li>                
            </ul>
            <section :class="['detailArea', {hide: locations.length && !currentBusiness.id} ]">               
                <section :class="[punClass, 'tacopun']">
                    <h1>{{pun}}</h1>
                    <img :class="['taco', {shake: loading === true}]" :src="require(`@/assets/taco-svgrepo-com.svg`)" alt="taco">
                </section>
            </section>
        </div>
        

    </div>
</template>

<script>
    import { mapGetters } from "vuex";
    import FilterForm from '@/components/Tacos/FilterForm.vue'
    export default {
        data() {
            return {
                puns: [
                    "Have a spec-taco-ler day!",
                    "This is a hostile taco'ver",
                    "Taco chance on me",
                    "Taco walk on the wild side"                    
                ],
                rand: 0,
                punclass: [
                    "red", 
                    "green", 
                    "yellow",
                    "orange"
                ]
            }
        },
        computed: {
            pun() {                
                return this.puns[this.rand];
            },
            punClass() {
                return this.punclass[this.rand];
            },
            ...mapGetters({
                locations: 'getLocations',
                currentBusiness: 'getCurrentBusiness',
                loading: 'getLoadingStatus'
            })
        },
        components: {
            FilterForm
        },
        methods: {
            randNum() {
                var rand = Math.floor(Math.random() * 4);
                this.rand = rand;
            },
            scroll() {
                window.onscroll = () => {
                let documentheight = Math.max(
                        Math.max(document.body.scrollHeight, document.documentElement.scrollHeight),
                        Math.max(document.body.offsetHeight, document.documentElement.offsetHeight),
                        Math.max(document.body.clientHeight, document.documentElement.clientHeight)
                    );
                    
                let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight  >= documentheight;
                    if (bottomOfWindow) {
                        let offset = parseInt(this.$store.getters.getOffset) + 20;
                        this.$store.commit('setOffset', offset)
                        this.$store.dispatch('fetchNewlocations')
                    }
                };
            }
        },
        filters: {
            convertTime(value) {
                if (!value) return ''
                let hours24 = parseInt(value.substring(0, 2), 10);
                let hours = ((hours24 + 11) % 12) + 1;
                let amPm = hours24 > 11 ? 'pm' : 'am';
                let minutes = value.substring(2)
                return hours + ':' + minutes  + amPm
            }
        },
        created() {
            this.randNum();
            this.scroll()
        },
          metaInfo() {
            return {
            title: 'Taco Shop Demo',
            meta: [
                {
                name: 'description',
                content: 'Using Vue.js, Vuex, and Vue-agile to pull and display data from the Yelp API.'
                }
            ]
            }
        }
    }
</script>

<style lang="scss">

    #tacopage {
        font-family: 'Spartan', Arial, sans-serif;
        font-size: 14px;
        width: 100vw;

        header {
            position: sticky;
            top: 50px;
            background-color: #FFFFFF;
            border-bottom: 1px solid #DDDDDD;
            z-index: 100;
            width: 100%;
            background-image: url('../../assets/taco_background.jpg');
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;

            h1 {
                color: #FFF;
                padding: .5em 1em;
                font-size: 3em;
                background-color: rgba(0,0,0,0.3);
                @media only screen and (max-width: 42em) {
                    font-size: 2em;
                    padding: .5em;
                }
            }
        }
    }
    .container {
        width: 80%;
        margin: 0 auto;
        @media only screen and (max-width: 27em) {
            width: 90%;
            margin: 0;
            padding: 0 1em;
        }
    }
    ul {
        margin: 0 1em 0 0;
        padding: 0;
        list-style-type: none;
        }
    ul#locations {
        width: 70%;
        > li a{
            display: flex;
            flex-direction: row;
            cursor: pointer;
            width: 100%;
            box-shadow:  0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
            margin: 1em 0;
            text-decoration: none;
            color: inherit;

            .thumbnail {
                margin-right: 1em;
                 img{
                    width: 60px;
                    height: 60px;
                }
            }
            .details {
                margin-top: 1em;
            }
            p {
                margin: 0 0 .5em 0;
            }
        }
        @media only screen and (max-width: 60em) {
            &.full {
                width: 90%;
                margin: 0 auto;
            }
        }
    }
    .content {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        padding: 1em;
        @media only screen and (max-width: 60em) {
            width: 100%;
            padding: 0;
            margin: 0;
        }
    }
    .VueCarousel {
        width: 100%;
        .VueCarousel-slide {
            text-align: center;
            img {
                height: 400px;
                width: auto;

            } 
        }
                   
    }
    
    .slide-enter-active {
    transition: 2s;
    }
    .slide-leave-active {
        transition: .25s;
    }
    .slide-enter {
    transform: translate(100%, 0);
    }
    .slide-leave-to {
    transform: translate(200%, 0);
    }
    .tacopun {
        min-height: 50vh;
        color: #FFFFFF;
        font-family: 'Kalam', cursive;
        font-size: 4em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 1em;
        position: sticky;
        top: 200px;
        width: 80%;
        margin: 0 auto;
        text-align: center;
        @media only screen and (max-width: 42em) {
            font-size: 3em;
            width: auto;
        }
        @media only screen and (max-width: 26em) {
            font-size: 2em;
            width: 100%;
        }
        &.red {
            background-color:#b71c1c;
        }
        &.green {
            background-color: #1b5e20;
        }
        &.yellow {
            background-color: #ffab00;
        }
        &.orange {
            background-color: #ff5722;
        }
    }
    #businessDetails {
        box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
        padding: 1em;
        width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        position: sticky;
        top: 200px;

        .details {
            width: 100%;
            h1 {
                font-size: 1.5em;
                margin-bottom: .5em;
            }
        }
        @media only screen and (max-width: 42em) {
            flex-direction: column;
        }
    }
    .rating {
        font-size: 18px;
        line-height: 1em;
        color:  #ffab00;
        margin-bottom: .5em;
    }
    .detailArea {
        width: 100%;
         @media only screen and (max-width: 60em) {
            &.hide {display: none;}
        }
    }
    .agile {
        width: 50%;
        @media only screen and (max-width: 42em) {
            width: 100%
        }
    }
    .agile img {
        height: 400px;
    }
    .agile__slides {
        max-width: 100%;
    }
    .agile__dots {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    padding: 0;
    white-space: nowrap;
}
 .agile__dot {
    margin: 0 10px;
}
.agile__dot button {
    background-color: #eee;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: block;
    height: 10px;
    font-size: 0;
    line-height: 0;
    margin: 0;
    padding: 0;
    -webkit-transition-duration: .3s;
    transition-duration: .3s;
    width: 10px;
}
.agile__dot--current button, .agile__dot:hover button {
    background-color: #888;
}
.agile__slide {
    text-align: center;
}
.pricing {
    font-size: 18px;
    color: #1b5e20;
}
.hours .day {
    width: 8em;
    display: inline-block;
}
.hours .header {
    font-weight: bold;
    margin: .25em 0;
    border-bottom: 1px solid #DDDDDD;
}
.hours li {
    margin: .25em 0;
}
.status {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bold;
    color:  #1b5e20;
    line-height:1em;
    font-size: 20px;
    ion-icon {
        height: 25px;
        width: 25px;
        margin-right: .5em;
    }
}
.transactions {
    span {
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        margin: 0 1em;
        ion-icon {
            height: 25px;
            width: 25px;
            &[name="checkmark-circle-outline"] {
                color: #1b5e20;
            }
            &[name="close-circle-outline"] {
                color: #b71c1c;
            }
        }
    }
}
.phone, .address {
    a {
        color: inherit;
        text-decoration: none;
        ion-icon {
            width: 25px;
            height: 25px;
            margin-right: .5em;
        }

    }
}
.address, .phone a{
    display: flex;
    flex-direction: row;
    align-items: center;
}
button {
        margin: .375rem;
    color: inherit;
    text-transform: uppercase;
    word-wrap: break-word;
    white-space: normal;
    cursor: pointer;
    border: 0;
    border-radius: .125rem;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    padding: .84rem 2.14rem;
    font-size: .81rem;
        color: #fff;
    background-color: #2e2e2e;
    cursor: pointer;
    border-radius: 4px;
    &:hover {
        box-shadow:  0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    }
    
}
img.taco {
        height: 100px;
        width: 100px;
        &.shake {
            animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
            transform: translate3d(0, 0, 0);
            backface-visibility: hidden;
            perspective: 1000px;
            animation-iteration-count: infinite;
        }
    }
    @keyframes shake {
        10%, 90% {
            transform: translate3d(-1px, 0, 0);
        }
        
        20%, 80% {
            transform: translate3d(2px, 0, 0);
        }

        30%, 50%, 70% {
            transform: translate3d(-4px, 0, 0);
        }

        40%, 60% {
            transform: translate3d(4px, 0, 0);
    }

}
</style>