<template>
  <div id="joblisting">
    <ul v-if="currentJobs.length > 0">
      <li
        v-for="job in currentJobs"
        :key="job.id"
        v-bind:class="['list-item', { featured: job.featured === true }]"
      >
        <span class="thumb">
          <img :src="require(`@/assets/${job.logo}`)" :alt="job.company" />
        </span>
        <span class="content">
          <span class="company">
            <span class="name">{{ job.company }}</span>
            <span class="tags">
              <span class="new" v-if="job.new">New</span>
              <span class="featured" v-if="job.featured">Featured</span>
            </span>
          </span>
          <span class="position">
            <span class="title">{{ job.position }}</span>
            <span class="tags" v-if="isMobile == false">
              <span
                class="tag"
                @click="changeFilters"
                data-action="add"
                data-type="role"
                :data-value="job.role"
                >{{ job.role }}</span
              >
              <span
                class="tag"
                @click="changeFilters"
                data-action="add"
                data-type="level"
                :data-value="job.level"
                >{{ job.level }}</span
              >
              <span
                class="tag"
                v-for="language in job.languages"
                :key="language"
                @click="changeFilters"
                data-action="add"
                data-type="language"
                :data-value="language"
                >{{ language }}</span
              >
              <span
                class="tag"
                v-for="tool in job.tools"
                :key="tool"
                @click="changeFilters"
                data-action="add"
                data-type="tool"
                :data-value="tool"
                >{{ tool }}</span
              >
            </span>
          </span>
          <span class="details">
            <span class="time">{{ job.postedAt }}</span>
            <span class="contract">{{ job.contract }}</span>
            <span class="location">{{ job.location }}</span>
          </span>
          <span class="position mobile" v-if="isMobile == true">
            <span class="tags">
              <span
                class="tag"
                @click="changeFilters"
                data-action="add"
                data-type="role"
                :data-value="job.role"
                >{{ job.role }}</span
              >
              <span
                class="tag"
                @click="changeFilters"
                data-action="add"
                data-type="level"
                :data-value="job.level"
                >{{ job.level }}</span
              >
              <span
                class="tag"
                v-for="language in job.languages"
                :key="language"
                @click="changeFilters"
                data-action="add"
                data-type="language"
                :data-value="language"
                >{{ language }}</span
              >
              <span
                class="tag"
                v-for="tool in job.tools"
                :key="tool"
                @click="changeFilters"
                data-action="add"
                data-type="tool"
                :data-value="tool"
                >{{ tool }}</span
              >
            </span>
          </span>
        </span>
      </li>
    </ul>
    <ul v-else>
      <li class="list-item no-results">No Results found</li>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      isMobile: false
    };
  },
  computed: {
    ...mapGetters({
      currentJobs: "jobList"
    })
  },

  methods: {
    ...mapActions({
      changeFilters: "changeFilters"
    }),
    onResize() {
      this.isMobile = window.innerWidth < 860;
    }
  },
  beforeMount() {
    this.onResize();
    window.addEventListener("resize", this.onResize, { passive: true });
  },
  beforeDestroy() {
    if (typeof window !== "undefined") {
      window.removeEventListener("resize", this.onResize, { passive: true });
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/_variables.scss";
#joblisting ul {
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  width: 80%;

  li.list-item {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #ffffff;

    margin: 1.5em 0;
    padding: 0.25em 0;
    box-shadow: 0 3px 6px rgba($primaryColor, 0.16),
      0 3px 6px rgba($primaryColor, 0.23);
    border-radius: 4px;
    border-width: 0 0 0 6px;
    border-style: solid;
    border-color: #ffffff;
    &.featured {
      border-color: $primaryColor;
    }
    .thumb {
      margin: 0 0.75em;
      img {
        width: 60px;
        height: 60px;
      }
    }
    .content {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
    .company {
      font-size: 0.85em;
      color: $primaryColor;
      margin: 0.25em 0.5em 0.25em 0;
      display: inline-block;

      .name {
        font-weight: bold;
        margin-right: 1em;
      }

      .tags > span {
        margin: 0 0.5em;
        border-radius: 10px;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        text-transform: uppercase;
        font-size: 0.75em;
        line-height: 0.75em;
        padding: 0.75em 0.5em 0.5em 0.5em;

        &.new {
          background-color: $primaryColor;
          color: #ffffff;
        }
        &.featured {
          background-color: $neutralVDark;
          color: #ffffff;
        }
      }
    }
    .position {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      .title {
        font-weight: bold;
        margin: 0.5em 0;
      }
      .tags {
        font-size: 0.75em;
        line-height: 0.75em;
        display: inline-flex;
        flex-direction: row;
        justify-content: flex-start;
        margin: 0 2em 0 0;

        .tag {
          background-color: $neutralLight_bg;
          color: $primaryColor;
          margin: 0 0.5em;
          padding: 0.5em;
          line-height: 1em;
          cursor: pointer;
          border-radius: 4px;

          &:hover {
            background-color: $neutralVDark;
            color: #ffffff;
          }
        }
      }
    }
    .details {
      font-size: 0.75em;
      color: $neutralDark;
      > span {
        &:after {
          content: "  ";
        }
        &:last-child:after {
          content: "";
        }
      }
    }
  }
}
@media only screen and (max-width: 54em) {
  #joblisting ul li.list-item {
    position: relative;
    margin-bottom: 2.5em;
    .thumb {
      top: -30px;
      position: absolute;
      height: 60px;
    }
    .content {
      padding: 2em 0.75em 1em 0.75em;
    }
    .company,
    .details {
      font-size: 1em;
    }
    .position {
      .title {
        font-size: 1.2em;
        margin: 1em 0;
      }
      &.mobile {
        border-top: 1px solid $neutralDark;
        padding: 1em 0;
        margin-top: 1em;

        .tags {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          font-size: 1em;
          .tag {
            margin: 0.5em 0.5em 0.5em 0;
          }
        }
      }
    }
  }
}
</style>
