<template>
    <div id="countriespage"  :class="{dark : isDark}">
    <Header />
    <div class="main_content">
    <FilterForm />
    <ul>
      <li v-for="country in countries" :key="country.alpha3Code" class="cardItem">
        <router-link :to="{name: 'Details', params: {slug: country.alpha3Code} }">
          <figure>
              <img :src="country.flag" :alt="country.name" loading=lazy>      
          </figure>
        
          <div class="details">
            <h1>{{ country.name }}</h1>
            <p><span class="header">Population:</span> {{country.population}}</p>
            <p><span class="header">Region:</span> {{country.region}}</p>
            <p><span class="header">Capital:</span> {{country.capital}}</p>
          </div>
        </router-link>
      </li>
    </ul>
  </div>
    </div>
</template>

<script>
// @ is an alias to /src
import {mapGetters, mapActions} from 'vuex';
import FilterForm from '@/components/Countries/FilterForm';
import Header from '@/components/Countries/Header';

export default {
  name: "Home",
  computed: {
    ...mapGetters({
      countries: 'getCountries',
      isDark: 'isDark'
    })
  },
  methods: {
    ...mapActions({
      fetchCountries: 'fetchCountries'
    })
  },
  created() {
      this.loading = true
      this.fetchCountries()
      .then(() => this.loading = false)
  },
  components: {
    FilterForm,
    Header
  },
  metaInfo() {
    return {
      title: 'REST Countries Demo',
      meta: [
        {
          name: 'description',
          content: 'Using Vue.js, Vuex and Vue-Router to pull and display data from the REST Countries API.'
        }
      ]
    }
  }
};
</script>
<style lang="scss">
    #countriespage {
        margin: 0;
        padding: 0;
        font-family: 'Nunito Sans', sans-serif;
        font-size: 14px;
        color: hsl(200, 15%, 8%);
        min-height: 100vh;
        &.dark {
            background-color:hsl(209, 23%, 22%);
            color: hsl(0, 0%, 100%);
        }
    }
</style>
<style scoped lang="scss">

 #countriespage {
    margin: 0;
    padding: 0;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 14px;
    color: hsl(200, 15%, 8%);
    min-height: 100vh;
    .main_content {
        min-height: calc(100vh - 80px);
        ul {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: flex-start;
            justify-content: space-between;
            width: 80%;
            margin: 0 auto;
            padding: 0;

            .cardItem {
                width: 300px;
                display: flex;
                flex-direction: column;
                align-items: stretch;
                box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
                margin: 1em 0;
                text-align: left;
                border-radius: 8px;
            }

            img {
                width: 100%;
                min-height: 150px;
                max-height: 150px;
                border-radius: 8px 8px 0 0;
            }
            figure {
                margin: 0;
                padding: 0;
            }
            .details {
                padding: .5em;
                height: 150px;
                margin: .5em;
            }
            p {
                padding: 0;
                margin: .5em 0;
            }
            h1 {
                font-size: 1.15em;
            }
            .header {
                font-weight: 600;
            }
            a {
                    text-decoration: none;
                    color: hsl(200, 15%, 8%);
                }
        }
           
        }
        &.dark {
            background-color:hsl(209, 23%, 22%);
            color: hsl(0, 0%, 100%);
            .cardItem {

                a {
                    color: hsl(0, 0%, 100%);
                }
            }
        }
    }  
</style>