<template>
  <div id="filters">
    <div id="display" v-if="tags.length > 0">
      <ul>
        <li v-for="tag in tags" :key="`tag_${tag.value}`" class="tag">
          <span
            class="value"
            @click="changeFilters"
            data-action="remove"
            :data-type="tag.category"
            :data-value="tag.value"
            >{{ tag.value }}</span
          >
          <span class="image"
            ><img
              :src="require('@/assets/icon-remove.svg')"
              alt="Remove"
              @click="changeFilters"
              :data-type="tag.category"
              :data-value="tag.value"
          /></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import store from "@/store/index";
export default {
  props: {
    data: Array
  },
  data() {
    return {
      isVisible: true
    };
  },
  computed: {
    ...mapGetters({
      filters: "getFilters",
      tags: "selectedFilters"
    })
  },
  methods: {
    ...mapActions({
      changeFilters: "changeFilters"
    }),
    removeFilter: function(event) {
      store.dispatch("removeFilter", event);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/_variables.scss";
#filters {
  width: 80%;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 3px 6px rgba($primaryColor, 0.16),
    0 3px 6px rgba($primaryColor, 0.23);
  #select {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
    padding: 1em;

    @media only screen and (max-width: 41em) {
      flex-wrap: wrap;
      > div {
        width: 45%;
      }
    }
    @media only screen and (max-width: 27em) {
      flex-direction: column;
      > div {
        width: 100%;
      }
    }
    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }
  }
  .close,
  .open {
    text-align: right;
    color: $neutralVDark;
    padding: 0.5em;
    cursor: pointer;
    font-size: 1.2em;
  }
  #display ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .tag {
      background-color: $neutralLight_bg;
      color: $primaryColor;
      margin: 0.5em;
      border-radius: 4px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      box-shadow: 0 3px 6px rgba($primaryColor, 0.16),
        0 3px 6px rgba($primaryColor, 0.23);
      cursor: pointer;

      .value {
        padding: 0.25em 0.5em;
      }

      .image {
        background-color: $primaryColor;
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        border-radius: 0px 4px 4px 0px;
        padding: 0em 0.25em;
        cursor: pointer;
        img {
          margin: auto;
        }
        &:hover {
          background-color: $neutralVDark;
        }
      }
    }
  }
}
</style>
