<template>
    <div id="filterForm">
         <form @submit.prevent="getLocations"><label for="location" style="display: none"></label><input type="text" name="location" v-model="location" id="location" placeholder="Enter Location (eg San Francisco, 02854, TX)"><button type="submit">Search</button></form>
         <form action="">
             <label for="sortby" style="display: none"></label>
            <select name="sortby" id="sortby" v-model="sortby">
                <option value="best_match">Best Match</option>
                <option value="rating">Rating</option>
                <option value="review_count">Review Count</option>
            </select>
        </form>
    </div>   
</template>

<script>
    export default {
        computed: {
            location: {
                get: function() {
                    return this.$store.getters.getLocation
                },
                set: function(value) {
                    this.$store.commit('setLocation', value)
                }
            },
            sortby: {
                get: function() {
                    return this.$store.getters.getSortBy
                },
                set: function(value) {
                    window.scrollTo(0, 0)
                    this.$store.commit('setSortBy', value)
                    this.$store.commit('setOffset', 0)
                    this.$store.dispatch('fetchLocations')
                }
            }
        },
        methods: {
           getLocations() {
               this.$store.dispatch('fetchLocations')
           }
        }
    }
</script>

<style scoped lang="scss">
    #filterForm {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 1em auto;
        @media only screen and (max-width: 74em) {
            flex-direction: column;
        }
    }
    form {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        width: 100%;
    }
    input[type="text"], select {
        font: 400 14px 'Nunito Sans', sans-serif;
        padding: .5em 1em;
        border: none;
        border-radius: 4px;
        box-shadow:  0 .125rem .25rem rgba(0,0,0,.075);
        outline: none;
    }
    input[type="text"] {
        width: 100%;
        margin-right: 1em;
    }
    .dark input[type="text"],.dark select {
        color:  hsl(0, 0%, 100%);
        background-color: hsl(207, 26%, 17%); 
    }
    button {
        margin: .375rem;
    color: inherit;
    text-transform: uppercase;
    word-wrap: break-word;
    white-space: normal;
    cursor: pointer;
    border: 0;
    border-radius: .125rem;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    padding: .84rem 2.14rem;
    font-size: .81rem;
        color: #fff;
    background-color: #2e2e2e;
    cursor: pointer;
    border-radius: 4px;
    &:hover {
        box-shadow:  0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    }
    @media only screen and (max-width: 74em) {
            padding: .5em 1em;
        }
}
</style>